* {
  box-sizing: border-box;
  font-family:"Poppins" , sans-serif;
  margin: 0;
  padding: 0;
}

body {
  background: #e5e5e5;
}

input {
  margin-bottom: 15px;
  text-align: center;
  padding: 15px 70px;
  background: #eeeeee;
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  border:none;
}

button {
  margin-bottom: 15px;
  border:none;
  text-align: center;
  padding: 10px 30px;
  font-size: 20px;
  border-radius: 25px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
  color: #ffffff;
  background: linear-gradient(90deg, #0038ff 15.43%, #00ffff 118.29%);
}

h1 {
  margin-bottom: 15px;
  font-size: 24px;
}

a{
  margin-bottom: 15px;
  font-size: 14px;
  text-decoration: none;
  color: #3E92AC;
}

.container {
  width: 771px;
  height: 516px;
  background-color: #ffffff;
  margin: 50px auto;
  border-radius: 15px;
}

.wrapper {
  text-align: center;
  padding: 35px;
}

.icons{
  position: absolute;
  padding: 15px 0 15px 50px;
  color: #BFBFBF;
}

/* Responsive media query */


/* for Tablet view */
 @media (max-width: 768px)
{
  .container {
    width: 550px;
    height: auto;

}
}


/* For Mobile View */
@media (max-width: 500px)
{
 .container {
   width: 350px;
   height: auto;
 }
}
